// apiClient.js
import axios from 'axios';

// Crez une instance Axios
const apiClient = axios.create({
  baseURL: process.env.VUE_APP_API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Ajoutez un intercepteur de requtes
apiClient.interceptors.request.use(
  (config) => {
    // Rcuprer le token depuis localStorage
    const token = localStorage.getItem('token');
    console.log('Token:', token); // Vrifiez si le token est rcupr

    // Si un token est prsent, l'ajouter aux en-ttes de la requte
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

export default apiClient;